		.main-spec { width: 100%; margin: 70px 0 0 0;}
		.main-dev { display: none;}
		.main-cont, .b-news, .b-usefull { flex-wrap: wrap;}
		.main-spec-box__img { width: 48%;}
		.main-spec .zag1 { margin: 0 0 35px 0;}
		.b-news-item { width: 48%; margin: 0 0 20px 0;}
		.b-usefull-left, .b-news-item img { width: 100%;}
		.b-usefull-right { width: 100%; margin: 20px 0 0 0;}
		.b-usefull-left img , .b-usefull-right img { max-width: 32%;}
		nav { display:block; position:absolute; text-align:center; width:100%; left:-100%; top:0; background:#2075a1; margin:0; padding:60px 0; z-index:25;}
		.b-menu .main { height: 44px;}
		.b-menu { background:#05283d;}
		.close { display: block;}
		.menu-but-w { display: flex; background: #2075a1;}
