	}
	@media only screen and (max-width:400px){
		.b-kat-item, .main-spec-box__img { width: 100%;}
		.b-kat-item { padding: 0 9.6% 60px 9.6%;}
		.b-usefull-left img, .b-usefull-right img { height: 170px;}
		.h-info { flex-direction: column; align-items: flex-end;}
		.h-info__phone { margin: 0 0 20px 0;}
		.h-zag { line-height: normal;}
		.b-news { margin: 15px 0 0 0;}
		.b-usefull { margin: 15px 0;}
		.zag2 { font-size: 24px;}
		.b-com-item__name { font-size: 18px;}
		.b-com-item-info { width: 60%;}
		.b-com-item { border: #fff solid 2px; padding: 14px 0;}
